import React from 'react';
import { Link, StaticQuery, graphql} from "gatsby";
import {Nav, NavItem} from "reactstrap";



function Footer() {
    return (
                <StaticQuery query={
                    graphql`
                {
                sporttalk{
                    pages{
                        id
                        title
                        in_footer
                      }
                    }
                }
                `
                } render={data =>
                    <React.Fragment>
                        <footer>
                        <Nav>
                            {data.sporttalk.pages.map((page, key)=>
                                (page.in_footer === true)?
                                <NavItem key={key}>
                                    <Link partiallyActive={true} className={"nav-link"} to={'/' + page.title.toLowerCase().replace(' ','_')} >{page.title}</Link>
                                </NavItem> : ''
                            )}
                        </Nav>
                            <div>
                                    {data.sporttalk.pages.map((page, key)=>
                                            (page.in_footer !== true)?
                                                    <Link key={key} partiallyActive={true} className={"nav-link"} to={'/' + page.title.toLowerCase().replace(' ','_')} >{page.title}</Link> : ''
                                        )}

                            </div>
                        </footer>
                    </React.Fragment>
                }/>
    );
}

export default Footer;
