import React from 'react';
import {StaticQuery, graphql, Link} from "gatsby"
import { Container, Row, NavItem, Nav } from "reactstrap";


const TopMenu = () => {
    return (
        <StaticQuery query={
            graphql`
            {
                sporttalk{
                    leagues{
                        id
                        name
                        club{
                            name
                        }
                        logo{
                            url
                        }
                    }
                }
            }
            `
        } render={data =>
            <React.Fragment>
                <Container>
                    <Row>
                    <Nav>
                        {data.sporttalk.leagues.map((league, key)=>
                            <NavItem key={key}>
                                <Link  className={"nav-link"} to={'/league/'+ league.name} >{league.name}</Link>
                            </NavItem>
                        )}
                    </Nav>
                    </Row>
                </Container>
            </React.Fragment>
        } />
    );
};

export default TopMenu;
